@import '../../node_modules/swiper/swiper-bundle.css';

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-ExtraBold.woff2") format("woff2");
  font-weight: 800;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Fira-Sans";
  src: url("../fonts/../fonts/FiraSans-Thin.woff2") format("woff2");
  font-weight: 100;
  font-display: swap;
  font-style: normal;
}

/* Reset styles */

* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a {
  color: inherit;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

nav ul {
  list-style: none;
}

img {
  vertical-align: top;
}

nav,
footer,
header,
aside,
label {
  display: block;
}

input,
button,
textarea {
  font-family: inherit;
  color: inherit;
}

::placeholder {
  color: inherit;
  font-family: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

sub {
  vertical-align: sub;
  font-size: smaller;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

:root {
  scroll-behavior: smooth;
}

html {
  scrollbar-gutter: stable;
}

.hide {
  opacity: 0;
}

.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #383B45;
  z-index: 100;
  display: grid;
  place-content: center;
}

.loader {
  display: grid;
  place-content: center;
  width: 125px;
  height: 125px;
  border: solid 10px rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-left-color: #4AF6CD;
  animation: load 1.3s linear infinite;
}

@keyframes load {
  100% {
    transform: rotate(360deg);
  }
}

/* global styles*/

body {
  font-family: "Fira-Sans", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 1rem;
  line-height: 1.6;
  text-transform: uppercase;
  color: #fff;
  background-color: #171919;
}

.title {
  margin-left: -50px;
}

.title h2 {
  display: inline-block;
  position: relative;
  font-weight: 800;
  font-size: 200px;
  line-height: 0.78;
  color: rgba(255, 255, 255, 0.02);
}

.title h2 span {
  display: inline-block;
  position: absolute;
  top: -20px;
  right: -14px;
  font-weight: 800;
  font-size: 80px;
  color: #fff;
}

.title h2 span::after {
  content: "";
  position: absolute;
  top: -15px;
  right: 0;
  width: 80px;
  height: 1px;
  background-color: #4AF6CD;
}

.headline-left {
  display: inline-block;
  position: relative;
  font-weight: 400;
  font-size: 22px;
  line-height: 1.7;
  letter-spacing: 7.33333px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.5);
}

.headline-left::before {
  content: "";
  position: absolute;
  left: -103px;
  top: 50%;
  width: 80px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5);
}

.slider-title {
  font-weight: 800;
  font-size: 40px;
  line-height: 1.5;
  text-transform: uppercase;
  color: #fff;
}

.no-scroll {
  overflow: hidden;
}

/* Header styles */

.header {
  position: relative;
  width: 100%;
  aspect-ratio: 1.4/1;
}

.header__slider {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  overflow: hidden;
}

.header__slide {
  position: relative;
  width: 100%;
  aspect-ratio: 1.4/1;
  background-color: #171919;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.header__slide::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(62.09% 62.09% at 42.87% 49.23%, rgba(34, 34, 34, 0.43) 0%, #0f0f0f 100%);
  z-index: 0;
}

.header__slide-map {
  position: absolute;
  top: -772px;
  right: -75px;
  z-index: 1;
}

.header__slide-content {
  position: absolute;
  right: 58px;
  bottom: 87px;
  text-align: right;
  max-width: 520px;
  z-index: 1;
}

.header__slide-subtitle {
  margin-bottom: 10px;
}

.header__slide-title {
  font-weight: 800;
  font-size: 80px;
  line-height: 1.1;
  text-transform: uppercase;
  margin-bottom: 27px;
}

.header__slide-subtext {
  margin-bottom: 35px;
}

.header__slide-text {
  margin-bottom: 20px;
}

.header__slide-link {
  position: relative;
  display: inline-block;
  width: 25px;
  height: 21px;
  z-index: 2;
}

.header__slide-link:hover .header__slide-content-arrow {
  fill: #4AF6CD;
  transition: all 0.3s;
}

.header__slide-content-arrow {
  width: 24px;
  height: 20px;
  fill: #fff;
}

.header__slider-control {
  position: absolute;
  top: 38px;
  right: 62px;
  z-index: 2;
  font-size: 0;
  width: 78px;
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 0px;
}

.header__slider-prev {
  width: 29px;
  height: 30px;
}

.header__slider-prev:hover .slider-arrow {
  fill: #4AF6CD;
  transition: all 0.3s;
}

.header__slider-next {
  width: 29px;
  height: 30px;
}

.header__slider-next:hover .slider-arrow {
  fill: #4AF6CD;
  transition: all 0.3s;
}

.header .swiper-pagination {
  position: absolute;
  left: auto;
  right: 67px;
  bottom: 22px;
  width: 100%;
  max-width: 920px;
  height: 44px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 0px;
}

.header .swiper-pagination-bullets .swiper-pagination-bullet {
  position: relative;
  margin: 0;
  width: 100%;
  max-width: 200px;
  height: 44px;
  border-top: 5px solid #fff;
  opacity: 1;
  border-radius: 0;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  gap: 0px;
}

.header .swiper-pagination-bullets .swiper-pagination-bullet::before {
  content: "";
  position: absolute;
  left: 0;
  top: -5px;
  width: 0;
  height: 5px;
  background-color: #4AF6CD;
  transition: all 0.3s;
}

.header .swiper-pagination-bullets .swiper-pagination-bullet-active {
  border-top: 3px solid transparent;
  transition: all 1s;
}

.header .swiper-pagination-bullets .swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  left: 0;
  top: -3px;
  width: 100%;
  height: 3px;
  background-color: #4AF6CD;
  transition: all 1s;
}

.header__pagination-number {
  font-size: 24px;
  line-height: 1.41667;
}

.header__pagination-shore {
  font-weight: 300;
  font-size: 16px;
  line-height: 1.1875;
  text-transform: capitalize;
}

.header__aside {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  max-width: 164px;
  border-right: 1px solid rgba(255, 255, 255, 0.25);
  z-index: 2;
}

.header__logo {
  display: block;
  width: 100%;
  height: 258px;
  text-align: center;
  padding-top: 22px;
}

.header__logo::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 181px;
  background-color: #4AF6CD;
  z-index: 0;
}

.header__logo-svg {
  position: relative;
  width: 86px;
  height: 258px;
  fill: #fff;
}

.header .slider-arrow {
  width: 24px;
  height: 20px;
  fill: #fff;
}

.header .road {
  animation-name: road-start;
  animation-fill-mode: forwards;
  animation-duration: 4s;
  animation-delay: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  stroke: #fff;
  transition: all 1s linear;
  stroke-dashoffset: 600;
  stroke-dasharray: 600;
}

@keyframes road-start {
  to {
    stroke-dashoffset: 0;
  }
}

.header .burger {
  display: none;
  width: 24px;
  height: 16px;
  position: relative;
  transition: all 0.3s;
  z-index: 10;
}

.header .burger:hover .burger__span {
  background-color: blue;
}

.header .burger__span {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2.2px;
  border-radius: 1px;
  background-color: red;
  transition: all 0.3s;
}

.header .burger__span-one {
  top: 0;
}

.header .burger__span-two {
  top: 50%;
}

.header .burger__span-three {
  top: 100%;
}

/* modals styles*/

.modal-window {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 15px;
  z-index: -50;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
  background: rgba(0, 0, 0, 0.5);
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal-window--visible {
  z-index: 50;
  opacity: 1;
  pointer-events: all;
}

@media (max-width: 768px) {
  .header .burger {
    display: block;
  }

  .header .untranslate {
    transform: translateX(0);
  }

  .header .span-one-transform {
    top: 50%;
    transform: rotate(45deg);
    transition: all 0.3s;
  }

  .header .span-two-transform {
    transform: rotate(-45deg);
    transition: all 0.3s;
  }

  .header .span-three-transform {
    display: none;
  }
}